/**
 * Optimised equality check for format objects.
 *
 * @param {?Object} format1 Format to compare.
 * @param {?Object} format2 Format to compare.
 *
 * @return {boolean} True if formats are equal, false if not.
 */export function isFormatEqual(format1,format2){// Both not defined.
if(format1===format2){return true;}// Either not defined.
if(!format1||!format2){return false;}if(format1.type!==format2.type){return false;}var attributes1=format1.attributes;var attributes2=format2.attributes;// Both not defined.
if(attributes1===attributes2){return true;}// Either not defined.
if(!attributes1||!attributes2){return false;}var keys1=Object.keys(attributes1);var keys2=Object.keys(attributes2);if(keys1.length!==keys2.length){return false;}var length=keys1.length;// Optimise for speed.
for(var i=0;i<length;i++){var name=keys1[i];if(attributes1[name]!==attributes2[name]){return false;}}return true;}